import React from 'react'
import Link from 'gatsby-link'

const IndexPage = () => (
  <div>
    <h1>Hello world!</h1>
    <p>I'm Wesley(saur) and welcome to my corner of the internet. I'll be posting projects here periodically, and providing my thoughts on things when I feel like it. This website is 50/50 a place for me to learn react, and for me to showcase anything I'm up to. Thanks for checking it out!
</p>
  </div>
)

export default IndexPage



